import Image from "next/image";
import React from "react";
import Button from "./Button";

export default function Homepage2() {
  return (
    <div
      style={{
        height: "100%",
        color: "white",
        width: "100%",
        padding: "20px",
        boxSizing: "border-box",
      }}
    >
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "space-around",
          alignItems: "center",
          minHeight: "90vh",
          flexWrap: "wrap", // ✅ makes it stack on smaller screens
        }}
      >
        {/* Left Content */}
        <div
          style={{
            flex: "1 1 400px", // ✅ responsive width
            maxWidth: "600px",
            padding: "20px",
            marginTop: "40px",
            textAlign: "left",
          }}
        >
          <span
            style={{
              fontSize: "clamp(28px, 5vw, 60px)", // ✅ responsive font size
              color: "rgba(213, 162, 53, 1)",
              fontWeight: "bold",
              lineHeight: "1.2",
              letterSpacing: "1px",
              display: "block",
              marginBottom: "20px",
            }}
          >
            Trusted partner for trading used equipments
          </span>

          <h4
            style={{
              fontSize: "clamp(12px, 2vw, 16px)",
              letterSpacing: "2px",
              fontWeight: "300",
              marginBottom: "20px",
              color: "#ddd",
            }}
          >
            Cherry Pickers Equipments – trusted for the best service.
          </h4>

          <Button content="Let's Connect" top="20px" />
        </div>

        {/* Right Image */}
        <div
          style={{
            flex: "1 1 400px", // ✅ responsive width
            maxWidth: "700px",
            position: "relative",
            height: "400px", // ✅ fixed height for image box
            marginTop: "20px",
          }}
        >
          <Image
            src="/assets/Banner1.png"
            alt="Banner"
            fill
            style={{
              objectFit: "contain",
            }}
          />
        </div>
      </div>
    </div>
  );
}
